@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400&subset=latin,latin-ext);

.landing {
    height: 100%;

    display: flex;

    align-items: flex-start;
    justify-content: flex-start;

    flex-direction: row;

    background-color: #f2f2f3;
    background-size: cover;
}

.landing .photo {
    position: absolute;
    margin-top: 7.5vh;
    margin-left: 50vh;
    width: 450px;
}

.landing .text-container {
    width: 80vw;
    display: flex;

    align-items: flex-start;
    justify-content: flex-start;

    flex-direction: column;

    margin-top: 15vh;
    margin-left: 10vw;
}

.landing .text-container h1 {
    font-family: "Roboto";
    font-style: bold;
    font-size: 2em;
    margin-top: 0px;
    margin-bottom: 10px;
}

.landing .text-container h2 {
    font-family: "Roboto";
    font-style: bold;
    font-size: 1.6em;
    margin-top: 0px;
    margin-bottom: 45px;
}

.landing .text-container h3 {
    font-family: "Roboto";
    font-style: bold;
    font-size: 1em;
    margin-bottom: 0px;
}

.landing .text-container p {
    font-family: "Roboto";
    font-style: normal;
    font-size: 1em;
    line-height: 1.35em;
    margin-top: 0px;
    margin-bottom: 0px;
}

.landing .text-container li {
    font-family: "Roboto";
    font-style: normal;
    font-size: 1em;
    margin-top: 0px;
    margin-bottom: 5px;
}

.landing .text-container .small-text {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 800;
    font-size: 0.875em;
    display: flex;

    color: #000000;
}

.landing .text-container .big-text {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 800;
    font-size: 2.25em;
    
    color: #000000;

    margin-top: 0.1em;
    margin-bottom: 1em;
}


.landing .button-container {
    width: 30vw; 
    
    display: flex;

    align-items: flex-end;
    justify-content: flex-end;

    flex-direction: column;
}